<template>
  <div>
    <van-popup
      v-model="show"
      closeable
      close-icon="close"
      position="bottom"
      :style="{ height: '' }"
    >
      <div class="popup">
        <van-form
          class="form"
          validate-first
          @failed="onFailed"
          @submit="submit"
        >
          <!-- 通过 pattern 进行正则校验 -->
          <span class="span_left">
            <span> 身份证号</span>
            <!-- <span style="color: red; margin-bottom: 5px">*</span> -->
          </span>

          <van-field
            v-model="value1"
            :border="false"
            name="身份证号"
            label=""
            maxlength="18"
            :clearable="true"
            placeholder="学生身份证号码"
            :rules="[
              { required: true, message: '请输入学生身份证号码' },
              { validator, message: '身份证号码格式错误！' },
            ]"
          >
            <!-- left-icon="contact" -->
            <!-- 身份证的扫描 -->
            <!-- <template #button>
              <van-icon class="scan" name="scan" @click="scanIdCard" />
            </template> -->
          </van-field>
          <span class="span_left">
            <span>现就读学校属于以下哪个类型</span>
            <!-- <span style="color: red; margin-bottom: 5px">*</span> -->
          </span>
          <!--  left-icon="home-o" -->
          <van-field name="radio" label="" :border="false">
            <template #input>
              <van-radio-group
                class="radio-group"
                v-model="radio"
                direction="horizontal"
              >
                <van-radio
                  v-for="(item, index) of studentList"
                  :key="index"
                  :name="item.studentType"
                  class="radio"
                  @click="clickRadio(item.tab)"
                >
                  {{ item.studentType }}
                </van-radio>

                <!-- <van-radio name="2">中小学</van-radio>
                <van-radio name="3">幼儿园</van-radio> -->
              </van-radio-group>
            </template>
          </van-field>
          <!-- <van-button
            round
            block
            type="primary"
            @click.prevent="value1 = '372522198302043119'"
            >点击此按钮可以填身份证，"懒人用法"</van-button
          > -->
          <div style="margin: 16px">
            <van-button round block type="info" native-type="submit"
              >提交</van-button
            >
          </div>
        </van-form>
      </div>
    </van-popup>
  </div>
</template>

<script>
import { mapState, mapActions, mapGetters } from "vuex";
import { Toast } from "vant";
import isIdcard from "../JS/index";
export default {
  name: "",
  components: {},

  data() {
    return {
      show: false,
      // 表单
      value1: "",
      value2: "",
      value3: "",
      pattern: false,
      radio: "",

      // studentList: [
      //   {
      //     studentType: "大学",
      //     tab: [
      //       {
      //         name: "方案一",
      //         CDPC: "111",
      //       },
      //       {
      //         name: "方案二",
      //         CDPC: "222",
      //       },
      //     ],
      //   },        {
      //     studentType: "大学",
      //     tab: [
      //       {
      //         name: "方案一",
      //         CDPC: "111",
      //       },
      //       {
      //         name: "方案二",
      //         CDPC: "222",
      //       },
      //     ],
      //   },     
      //   {
      //     studentType: "幼儿园",
      //     tab: [
      //       {
      //         name: "方案五",
      //         CDPC: "333",
      //       },
      //       {
      //         name: "方案六",
      //         CDPC: "444",
      //       },
      //     ],
      //   },
      // ],

      newTba: "",
    };
  },

  created() {
    console.log(this.studentList, "studentList");
    if (JSON.parse(sessionStorage.getItem("whiteList_json"))) {
      this.value1 = JSON.parse(sessionStorage.getItem("whiteList_json")).IDcard;
      this.show = true;
    } else {
      this.value1 = "";
    }

    this.radio = this.studentList[0].studentType;
    this.newTba = this.studentList[0].tab;
  },
  mounted() {},
  methods: {
    // 点击学校按钮的时候
    clickRadio(item) {
      this.newTba = item;
    },
    // 表单成功的回调

    submit() {
      // 将跳转的信息转为JSON字符串
      let string = JSON.stringify(this.newTba);
      let whiteList_json = {
        IDcard: this.value1,
        radio: this.radio,
      };
      // 将此页面的信息存在缓存
      sessionStorage.setItem("whiteList_json", JSON.stringify(whiteList_json));
      this.$router.push({
        path: "/planPage",
        query: {
          tab: string,
        },
      });
    },
    // 扫描身份证
    scanIdCard() {
      this.$router.push({
        path: "/scanIdCard",
      });
    },

    // 让弹出框显示

    showPopup() {
      this.show = true;
    },

    // 表单
    // 异步校验函数返回 Promise
    asyncValidator(val) {
      return new Promise((resolve) => {
        Toast.loading("验证中...");

        setTimeout(() => {
          Toast.clear();
          resolve(/\d{6}/.test(val));
        }, 1000);
      });
    },
    onFailed(errorInfo) {
      console.log("failed", errorInfo);
    },
    // 身份证验证
    validator(val) {
      return isIdcard(val);
    },
  },
  watch: {},
  computed: {
    ...mapState({
      studentList: (state) =>
        //  JSON.parse(state.app.studentJosnLsit)
        state.app.studentJosnLsit.studentList,
    }),
  },
};
</script>

<style lang='less' scoped>
.form {
  margin-top: 5vh;
}
.popup {
  padding: 15px;
}

:deep(.van-cell) {
  padding: 20px;
}
.scan {
  cursor: pointer;
}
.scan:hover {
  color: red;
  cursor: pointer;
}
.span_left {
  display: flex;
  font-weight: 700;
}
/deep/ .van-popup__close-icon--top-right {
  color: #000;
}
.radio {
padding: 2vw;
}

//  /deep/.van-field__control:nth-child(2) {
//   border-bottom: 1px solid #000;
// }
/deep/ input {
  border-bottom: 1px solid #e2dfdf;
}
.radio-group {
  display: flex;
}
</style>
