<template>
  <div>
    <div class="view-box Router">
      <div class="image">
        <div class="van-image loading-image" style="width: 100vw">
          <img :src="imgSrc" class="van-image__img" alt="" />
        </div>
      </div>
      <van-button class="button" type="info" @click="clickBut()"
        >立即投保</van-button
      >
      <popup ref="popup"></popup>
    </div>
  </div>

  <!-- <div class="top">
    <img :src="imgSrc" alt="" />
    <van-button class="button" type="info" @click="clickBut()"
      >立即投保</van-button
    >

    <popup ref="popup"></popup>
  </div> -->
</template>

<script>
// 引入自己写的弹框组件
import popup from "./components/popup.vue";
export default {
  name: "",
  components: { popup },

  data() {
    return {
      imgSrc: "",
    };
  },

  created() {},
  mounted() {
    // 存储的值，给白名单验证用，判断是否为学平险的白名单
    sessionStorage.setItem("XPXWHITE", 1);
    console.log(location.pathname, location);
    // 判断是否有无网
    // if (!navigator.onLine) {
    //   // this.imgSrc =
    //   //   "https://elife.ccb-life.com.cn/statics/cardTemp/cardImg/KDPC006.jpg";
    //   this.imgSrc = location.origin + this.$store.state.app.studentJosnLsit.src;
    // } else {
    //   this.imgSrc = require("../../assets/img/KDPC001.jpg");
    // }

    if (this.$store.state.app.studentJosnLsit.src) {
      this.imgSrc = location.origin + location.pathname.replace("/elife-card", '') + this.$store.state.app.studentJosnLsit.src;
    } else {
      this.imgSrc = require("../../assets/img/KDPC001.jpg");
    }
  },
  methods: {
    // 点击下面的立即投保，弹出弹框
    clickBut() {
      this.$refs.popup.showPopup();
    },
  },
  watch: {},
  computed: {},
};
</script>

<style lang='less' scoped>
.top {
  width: 100%;

  overflow-y: auto;

  height: 100%;
  img {
    width: 100%;
    height: calc(93.5vh - 4vw);

    /*元素的位置相对于浏览器窗口是固定位置*/
    // position: fixed;
    left: 0;
    top: 0;
  }
}
.button {
  bottom: 0;
  height: 12vw;
  padding: 0 20px;
  font-size: 4.8vw;
  border-radius: 18px;
  width: 100%;
}

.Router {
  position: absolute;
  left: 0;
  right: 0;
  height: 100vh;
  width: 100vw;
}
.view-box .image {
  height: calc(93.5vh - 4vw);
  overflow-y: auto;
}
</style>
